/**** Vets Styling ****/

@import 'https://fonts.googleapis.com/css?family=Cabin:400,400i,500,500i,600,600i,700,700i';

.page-veterans, .node-type-section-landing, .veterans_branded, .node-type-veterans {

	@media (min-width: 1101px) {
		#block-boxes-vet-search-icon-mobile {
			display: none;
		}
	}

	@media (max-width: 1101px) {
	#block-boxes-vet-search-icon {
		display: none;
	}
	}

	#main-nav {
		position: relative;
		z-index: 10; //SF-472
		//z-index: 999;
	}

	/****** SF Vet Menu and Header *******/

	.vet-sub-menu {

		#block-boxes-vet-search-icon {
			float: right;
			margin-top: -50px;
			margin-right: 50px;
		}
		.boxes-box .boxes-box-controls {
			display: none;
		}

		section#block-icfi-veterans-icfi-veterans-header {
			background-color: #35723C;
			z-index: -1;
			margin-bottom: 0px;

			#veterans-header {
				width: 1170px;
				margin: auto;
				padding: 2em 0;
				overflow: auto;

				#veterans-header-logo {
					background-color: #35723C;
					height: 90px;
					position: relative;
					overflow: auto;
					padding: 1.5em 0px;
					margin: 0px auto !important;
					z-index: 1;
					float:left;
					padding-right: 1em;

					img {
						width: 26em;
					}
				}

				#veterans-header-divider {
					border-left: solid 2px #ffffff;
					height: 90px;
					width: 2px;
					overflow: auto;
					float: left;
				}

				#veterans-header-text {
					float: left;
					width: 50%;
					font-size: 20px;
					color: #ffffff;
					font-weight: normal;
					line-height: 25px;
					padding-left: 1em;

				}

			}
		}

		background-color: #000;
		position: relative;
		z-index: 1;


		ul {
			list-style-type: none;
			width:1170px;
			margin: auto;
		}

		li {
			display: inline-block;
		}

		li a {

			color: #fff;
			padding: 16px;
			font-size: 16px;
			border: none;
			cursor: pointer;
			text-decoration: none;

		}

		li a:hover, li a.active {
			background: #606060
		}

		ul#superfish-1 li > ul {
			display: none;
			position: absolute;
			background: #f9f9f9 !important;
			min-width: 200px;
			padding: 0 !important;
			border-top-right-radius: 0px;
			border-bottom-left-radius: 0px;
			box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
			top: 48px;
			-moz-transition: none;
			-webkit-transition: none;
			-o-transition: color 0 ease-in;
			transition: none;
		}

		ul#superfish-1 li > ul li a {
			color: black;
			padding: 12px 16px;
			text-decoration: none;
			display: block;
			cursor: pointer;
			text-align: left;
		}

		ul#superfish-1 li > ul li a:hover, ul#superfish-1 li > ul li a:active {
			background-color: #EBEBEB;
		}

		ul#superfish-1 li a span.sf-sub-indicator {
			display:none;
		}

		.smokefreevet_nav .submenu {
			display: table-cell;
			text-align: center;
		}

		.smokefreevet_nav .submenu:hover .dropdown-content {
			display: block;
		}

		.smokefreevet_nav .submenu:hover .dropbtn {
			background-color: #606060;
		}

		ul.sf-expanded {
			margin-top: 1.4em !important;
			background-color: #fff;
			box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
		}

		ul.sf-expanded li {
			background: #fff;
			display: none;
			margin-bottom: 0px;
		}

		.sf-accordion-toggle a#superfish-1-toggle {

			color: #36723F;
			font-size: 25px;
			font-weight: normal;
			text-decoration:none;
			padding: 0.7em 0 0 0.7em;
			display: block;

		}

		.sf-accordion-toggle a#superfish-1-toggle span {

		}

		ul.sf-expanded li, ul.sf-expanded li a {
			display: block;
			/*padding: 10px 16px 10px 30px;*/
			font-size: 18px;
			color: black !important;
			text-decoration: none !important;
			font-weight: normal !important;
			cursor: pointer;
		}

		ul.sf-expanded li:hover, ul.sf-expanded li a:hover, ul.sf-expanded li a.active {
			background-color: #ECECEC;
		}

		.smokefreevet_nav #smokefreevet-accordion #sfv-collapse-menu .sub_menu a{
			padding-left: 60px;
		}

		.smokefreevet_nav i.fa.fa-chevron-right {
			position: relative;
			right: -15px;
			top: 5px;
			float: right;
			padding-left: 22px;
			font-size: 20px;
			padding-right: 19px;
		}

		.smokefreevet_nav #sfv-collapse-menu {
			padding-right: 15px;
			box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
			padding: 12px 0px;
		}

		.smokefreevet_nav .panel,
		.smokefreevet_nav .panel-heading {
			border-radius: 0px;
			border: 0px;
		}

	} /**** End of Desktop styling for .vet-sub-menu ****/

	@media (max-width: 991px) and (min-width: 768px) {
		.hidden-sm {
			display: none!important;
		}
	}

	#block-boxes-vet-search-icon-mobile {
		float: right;
		margin-top: -50px;
		margin-right: -10px;
	}

} /***** End of Veterans Header styling *****/

