/******** Quiz Styling *********/

.smokefreegov-why-i-use-tobacco-quiz {

	.webform-client-form {
		width:100%;
	}

	.form-group {
		margin-bottom: 15px;
		border-bottom: 1px solid #ADADAD;
		padding-bottom: 20px !important;
		overflow: auto;
	}

	div.form-radios {
		width: 100%;
	}

	.form-group > label {
		float: left;
		font-size: 24px;
		font-weight: normal;
	}

	input.form-radio {
		position: absolute;
		opacity: 0;
		float: right;
		margin: 0px;
		border: 0px;
		overflow: hidden;
	}


	.form-radio-label-no, .form-radio-label-yes {
		min-width: 10em;
		background-color: #ffffff;
		color: #2A2A2A;
		border-radius: 0px;
		margin: 0px;
		border: 1px solid #DADADA;
		padding: 1em 3em !important;
		text-align: center;
		float: right;
		text-indent: 0px !important;
		margin-left: 0px !important;
	}

	.form-radio-no:hover, .form-radio-label-no:hover, .form-radio-label-yes:hover {
		border: 1px solid #DADADA;
		background-color: #eee;
	}

	input:checked + label {
		background-color: #757576 !important;
		color:#ffffff;
	}

	.webform-progressbar {
		margin-bottom: 5em;
	}

	.webform-progressbar .webform-progressbar-outer {
		position: relative;
		border: none;
		width: 100%;
		height: 2em;
		margin: 0.35em -1px 2em;
		background-color: white;
	}

	.webform-progressbar .webform-progressbar-outer .webform-progressbar-inner {
		height: 2em;
		background: repeating-linear-gradient(135deg, #aaaaaa, #aaaaaa 5px, #777777 0px, #777777 20px);
	}

	.webform-progressbar-page {
		display: none !important;
	}

	.webform-progressbar-number {
		position: relative;
		z-index: 999;
		margin-top: -3.2em;
		text-align: center;
		width: 100%;
		font-size: 20px;
	}


} /***End Quiz Styling ***/


